@media (max-width: 767px) {
  .footer__user-block {
    grid-column: 1/-1;
    grid-row: 3/4;
    margin-left: 0;
    padding-bottom: 45px;
  }
}

.footer__donate {
  margin-bottom: 50px;
}
@media (max-width: 1160px) {
  .footer__donate {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .footer__donate {
    display: none;
  }
}

.footer__donate-link {
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 14px;
  padding-left: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #4c6c72;
  background-color: #fff;
}
.footer__donate-link svg {
  margin-left: 18px;
}

.footer__contacts,
.footer__blind-version {
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
}

@media (max-width: 767px) {
  .footer__blind-version {
    margin-bottom: 0;
  }
}

.footer__social {
  margin-left: auto;
  grid-column: 3/4;
  grid-row: 2/3;
}
@media (max-width: 1160px) {
  .footer__social {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}

.footer__lang {
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .footer__lang {
    display: none;
  }
}